import React, { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '../../firebase';
import { useAuth } from '../../context/AuthContext';
import { FiEye, FiFileText, FiSearch, FiPrinter, FiX } from 'react-icons/fi';
import POPrintPreview from '../Admin/POPrintPreview.jsx';

export default function VendorPOList() {
  const { vendorProfile } = useAuth();
  const [pos, setPOs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showPreview, setShowPreview] = useState(false);
  const [previewPO, setPreviewPO] = useState(null);
  const [search, setSearch] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');

  useEffect(() => {
    const load = async () => {
      if (!vendorProfile?.id) return;
      setLoading(true);
      const snap = await getDocs(query(collection(db, 'purchaseOrders'), where('vendorId', '==', vendorProfile.id)));
      const data = snap.docs.map(d => ({ id: d.id, ...d.data() }));
      setPOs(data);
      setLoading(false);
    };
    load();
  }, [vendorProfile]);

  const openPreview = (po) => { setPreviewPO(po); setShowPreview(true); };
  const closePreview = () => { setPreviewPO(null); setShowPreview(false); };

  if (loading) return <div className="p-6">Loading...</div>;

  const filtered = pos.filter(p => {
    const text = `${p.poNumber || ''} ${p.description || ''}`.toLowerCase();
    const matchesText = text.includes(search.toLowerCase());
    const matchesStatus = statusFilter === 'all' ? true : (String(p.status || 'pending') === statusFilter);
    return matchesText && matchesStatus;
  });

  return (
    <div className="space-y-6">
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-200 flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <div className="w-10 h-10 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-xl flex items-center justify-center">
            <FiFileText className="text-white" />
          </div>
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Purchase Orders</h1>
            <p className="text-gray-600">Read-only list of your purchase orders</p>
          </div>
        </div>
        <div className="flex items-center gap-3">
          <div className="relative">
            <FiSearch className="absolute left-3 top-3 text-gray-400" />
            <input
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              placeholder="Search PO No / Description"
              className="pl-9 pr-3 py-2 border rounded-lg text-sm"
            />
          </div>
          <select value={statusFilter} onChange={(e)=>setStatusFilter(e.target.value)} className="px-3 py-2 border rounded-lg text-sm">
            <option value="all">All</option>
            <option value="pending">Pending</option>
            <option value="approved">Approved</option>
            <option value="completed">Completed</option>
            <option value="cancelled">Cancelled</option>
          </select>
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">PO Number</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Description</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Amount</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filtered.map(po => (
                <tr key={po.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">{po.poNumber || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{po.description || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">₹{parseFloat(po.finalAmount || po.totalAmount || 0).toLocaleString()}</td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-gray-100 text-gray-800">{po.status || 'pending'}</span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <button onClick={() => openPreview(po)} className="text-gray-600 hover:text-gray-900" title="Preview / Print">
                      <FiEye size={16} />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {showPreview && previewPO && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white w-full max-w-6xl rounded-xl overflow-hidden shadow-2xl">
            <div className="flex items-center justify-between p-4 border-b bg-gray-50">
              <div className="font-semibold">PO Preview - {previewPO.poNumber || ''}</div>
              <div className="flex items-center gap-2">
                <button onClick={() => window.print()} className="inline-flex items-center px-3 py-1.5 bg-indigo-600 text-white rounded-lg text-sm hover:bg-indigo-700">
                  <FiPrinter className="mr-2" /> Print / Save PDF
                </button>
                <button onClick={closePreview} className="inline-flex items-center px-3 py-1.5 border rounded-lg text-sm">
                  <FiX className="mr-1" /> Close
                </button>
              </div>
            </div>
            <div className="p-4 max-h-[80vh] overflow-auto">
              <POPrintPreview poData={previewPO} vendorId={vendorProfile?.id} vendor={vendorProfile} onClose={closePreview} autoPrint={false} />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
