import React, { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '../../firebase';
import { useAuth } from '../../context/AuthContext';
import { FiFileText, FiTruck, FiActivity, FiArrowRight } from 'react-icons/fi';
import { Link } from 'react-router-dom';

export default function VendorDashboard() {
  const { vendorProfile } = useAuth();
  const [stats, setStats] = useState({ pos: 0, dcs: 0, ongoing: 0 });

  useEffect(() => {
    const load = async () => {
      if (!vendorProfile?.id) return;
      const [poSnap, dcSnap, jobSnap] = await Promise.all([
        getDocs(query(collection(db, 'purchaseOrders'), where('vendorId', '==', vendorProfile.id))),
        getDocs(query(collection(db, 'deliveryChallans'), where('vendorId', '==', vendorProfile.id))),
        getDocs(query(collection(db, 'jobAssignments'), where('vendorId', '==', vendorProfile.id)))
      ]);
      setStats({ pos: poSnap.size, dcs: dcSnap.size, ongoing: jobSnap.size });
    };
    load();
  }, [vendorProfile]);

  return (
    <div className="space-y-6">
      <div className="bg-gradient-to-r from-indigo-600 via-blue-600 to-purple-600 p-6 rounded-2xl shadow-sm text-white">
        <h1 className="text-2xl font-bold">Welcome, {vendorProfile?.name || 'Vendor'}</h1>
        <p className="text-blue-100">Here is a quick overview of your current activity.</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <StatCard icon={<FiFileText className="text-indigo-600" />} title="Purchase Orders" value={stats.pos} to="/vendor/pos" />
        <StatCard icon={<FiTruck className="text-purple-600" />} title="Delivery Challans" value={stats.dcs} to="/vendor/delivery-challans" />
        <StatCard icon={<FiActivity className="text-emerald-600" />} title="Ongoing Work" value={stats.ongoing} to="/vendor/ongoing-work" />
      </div>

      <div className="bg-white p-6 rounded-xl border border-gray-200">
        <div className="flex items-center justify-between">
          <div>
            <div className="text-lg font-semibold text-gray-900">Quick Actions</div>
            <div className="text-gray-600 text-sm">Jump directly to your work areas</div>
          </div>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mt-4">
          <QuickLink to="/vendor/pos" label="View Purchase Orders" />
          <QuickLink to="/vendor/delivery-challans" label="View Delivery Challans" />
          <QuickLink to="/vendor/ongoing-work" label="Update Ongoing Work" />
        </div>
      </div>
    </div>
  );
}

function StatCard({ icon, title, value, to }) {
  return (
    <Link to={to} className="bg-white p-6 rounded-xl shadow-sm border border-gray-200 flex items-center justify-between hover:shadow-md transition-shadow">
      <div className="flex items-center">
        <div className="w-12 h-12 bg-gray-50 rounded-xl flex items-center justify-center mr-4">
          {icon}
        </div>
        <div>
          <div className="text-sm text-gray-500">{title}</div>
          <div className="text-2xl font-bold text-gray-900">{value}</div>
        </div>
      </div>
      <FiArrowRight className="text-gray-400" />
    </Link>
  );
}

function QuickLink({ to, label }) {
  return (
    <Link to={to} className="group border border-gray-200 rounded-xl p-4 flex items-center justify-between hover:bg-gray-50">
      <div className="text-gray-800 font-medium">{label}</div>
      <FiArrowRight className="text-gray-400 group-hover:text-gray-600" />
    </Link>
  );
}
