import React, { useEffect, useMemo, useState } from 'react';
import { collection, getDocs, query, where, doc, updateDoc, serverTimestamp } from 'firebase/firestore';
import { db } from '../../firebase';
import { useAuth } from '../../context/AuthContext';
import { FiActivity, FiCheckCircle, FiClock, FiSave, FiSearch } from 'react-icons/fi';

const STATUS_OPTIONS = ['not_started', 'in_progress', 'blocked', 'pending_review', 'completed'];

export default function VendorOngoingWork() {
  const { vendorProfile } = useAuth();
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [savingId, setSavingId] = useState(null);
  const [filter, setFilter] = useState('all');
  const [search, setSearch] = useState('');

  const filtered = useMemo(() => {
    const base = filter === 'all' ? jobs : jobs.filter(j => (j.status || 'not_started') === filter);
    if (!search.trim()) return base;
    const s = search.toLowerCase();
    return base.filter(j => `${j.title || j.name || j.reference || ''} ${j.poNumber || ''}`.toLowerCase().includes(s));
  }, [jobs, filter, search]);

  useEffect(() => {
    const load = async () => {
      if (!vendorProfile?.id) return;
      setLoading(true);
      const snap = await getDocs(query(collection(db, 'jobAssignments'), where('vendorId', '==', vendorProfile.id)));
      const data = snap.docs.map(d => ({ id: d.id, ...d.data(), _dirty: false }));
      setJobs(data);
      setLoading(false);
    };
    load();
  }, [vendorProfile]);

  const updateLocal = (id, patch) => {
    setJobs(prev => prev.map(j => j.id === id ? { ...j, ...patch, _dirty: true } : j));
  };

  const saveJob = async (job) => {
    try {
      setSavingId(job.id);
      const ref = doc(db, 'jobAssignments', job.id);
      const payload = {
        status: job.status || 'in_progress',
        progress: Number(job.progress || 0),
        vendorNotes: job.vendorNotes || '',
        vendorUpdatedAt: serverTimestamp(),
      };
      await updateDoc(ref, payload);
      setJobs(prev => prev.map(j => j.id === job.id ? { ...j, ...payload, _dirty: false } : j));
    } catch (e) {
      console.error('Failed to update job', e);
      alert('Failed to update work status.');
    } finally {
      setSavingId(null);
    }
  };

  if (loading) return <div className="p-6">Loading...</div>;

  return (
    <div className="space-y-6">
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-200 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <div className="flex items-center space-x-3">
          <div className="w-10 h-10 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-xl flex items-center justify-center">
            <FiActivity className="text-white" />
          </div>
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Ongoing Work</h1>
            <p className="text-gray-600">Update the status and progress of your assigned jobs</p>
          </div>
        </div>
        <div className="flex items-center gap-3">
          <div className="relative">
            <FiSearch className="absolute left-3 top-3 text-gray-400" />
            <input value={search} onChange={(e)=>setSearch(e.target.value)} placeholder="Search job / PO number" className="pl-9 pr-3 py-2 border rounded-lg text-sm" />
          </div>
          <select value={filter} onChange={(e) => setFilter(e.target.value)} className="px-3 py-2 border rounded-lg text-sm">
            <option value="all">All</option>
            {STATUS_OPTIONS.map(s => <option key={s} value={s}>{s.replace('_',' ')}</option>)}
          </select>
        </div>
      </div>

      <div className="space-y-3">
        {filtered.length === 0 && (
          <div className="bg-white p-10 rounded-xl border border-gray-200 text-center">
            <div className="text-gray-800 font-medium mb-1">No jobs found</div>
            <div className="text-gray-600 text-sm">Try adjusting filters or search terms.</div>
          </div>
        )}
        {filtered.map(job => (
          <div key={job.id} className={`bg-white p-5 rounded-xl border ${job._dirty ? 'border-indigo-300' : 'border-gray-200'} shadow-sm`}
          >
            <div className="flex items-start justify-between gap-4">
              <div>
                <div className="text-sm text-gray-500">Job</div>
                <div className="text-lg font-semibold text-gray-900">{job.title || job.name || job.reference || 'Job'}</div>
                <div className="text-sm text-gray-600 mt-1">PO: {job.poNumber || job.poId || '-'} | Due: {job.dueDate?.toDate?.()?.toLocaleDateString?.() || job.dueDate || '-'}</div>
              </div>
              <div className="flex items-center gap-2">
                <span className={`inline-flex items-center px-2.5 py-1 text-xs rounded-full 
                  ${
                    (job.status||'not_started')==='completed' ? 'bg-emerald-100 text-emerald-700' :
                    (job.status||'not_started')==='in_progress' ? 'bg-blue-100 text-blue-700' :
                    (job.status||'not_started')==='blocked' ? 'bg-red-100 text-red-700' :
                    (job.status||'not_started')==='pending_review' ? 'bg-amber-100 text-amber-700' :
                    'bg-gray-100 text-gray-700'
                  }`}>
                  {job.status || 'not_started'}
                </span>
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Status</label>
                <select value={job.status || 'in_progress'} onChange={(e) => updateLocal(job.id, { status: e.target.value })} className="w-full px-3 py-2 border rounded-lg">
                  {STATUS_OPTIONS.map(s => <option key={s} value={s}>{s.replace('_',' ')}</option>)}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Progress (%)</label>
                <input type="number" min="0" max="100" value={job.progress ?? 0} onChange={(e) => updateLocal(job.id, { progress: Math.max(0, Math.min(100, Number(e.target.value))) })} className="w-full px-3 py-2 border rounded-lg" />
                <div className="w-full bg-gray-100 rounded-full h-2 mt-2">
                  <div className="h-2 rounded-full bg-indigo-600" style={{ width: `${Math.max(0, Math.min(100, Number(job.progress ?? 0)))}%` }} />
                </div>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Notes</label>
                <input type="text" value={job.vendorNotes || ''} onChange={(e) => updateLocal(job.id, { vendorNotes: e.target.value })} className="w-full px-3 py-2 border rounded-lg" placeholder="Add any blockers or updates" />
              </div>
            </div>

            <div className="flex items-center justify-end mt-4">
              <button onClick={() => saveJob(job)} disabled={savingId === job.id || !job._dirty} className="inline-flex items-center px-4 py-2 bg-indigo-600 text-white rounded-lg disabled:opacity-60">
                <FiSave className="mr-2" />
                {savingId === job.id ? 'Saving...' : 'Save Update'}
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
