import React, { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '../../firebase';
import { useAuth } from '../../context/AuthContext';
import { FiTruck, FiSearch, FiPrinter, FiX } from 'react-icons/fi';
import DeliveryChallanPreview from './DeliveryChallanPreview.jsx';

export default function VendorDeliveryChallanList() {
  const { vendorProfile } = useAuth();
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [showPreview, setShowPreview] = useState(false);
  const [previewDC, setPreviewDC] = useState(null);

  useEffect(() => {
    const load = async () => {
      if (!vendorProfile?.id) return;
      setLoading(true);
      const snap = await getDocs(query(collection(db, 'deliveryChallans'), where('vendorId', '==', vendorProfile.id)));
      const data = snap.docs.map(d => ({ id: d.id, ...d.data() }));
      setItems(data);
      setLoading(false);
    };
    load();
  }, [vendorProfile]);

  if (loading) return <div className="p-6">Loading...</div>;

  const filtered = items.filter(dc => {
    const text = `${dc.challanNumber || ''} ${dc.poNumber || ''} ${dc.vendorName || ''}`.toLowerCase();
    return text.includes(search.toLowerCase());
  });

  const openPreview = (dc) => { setPreviewDC(dc); setShowPreview(true); };
  const closePreview = () => { setPreviewDC(null); setShowPreview(false); };

  return (
    <div className="space-y-6">
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-200 flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <div className="w-10 h-10 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-xl flex items-center justify-center">
            <FiTruck className="text-white" />
          </div>
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Delivery Challans</h1>
            <p className="text-gray-600">Read-only list of your delivery challans</p>
          </div>
        </div>
        <div className="relative">
          <FiSearch className="absolute left-3 top-3 text-gray-400" />
          <input value={search} onChange={(e)=>setSearch(e.target.value)} placeholder="Search Challan/PO/Vendor" className="pl-9 pr-3 py-2 border rounded-lg text-sm" />
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Challan No</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">PO Number</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Total Qty</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Total Amount</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filtered.map(dc => (
                <tr key={dc.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">{dc.challanNumber || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{dc.date || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{dc.poNumber || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{dc.totalQuantity || 0}</td>
                  <td className="px-6 py-4 whitespace-nowrap">₹{parseFloat(dc.totalAmount || 0).toLocaleString()}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm">
                    <button onClick={() => openPreview(dc)} className="text-gray-600 hover:text-gray-900" title="Preview / Print">
                      <FiPrinter size={16} />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {showPreview && previewDC && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white w-full max-w-6xl rounded-xl overflow-hidden shadow-2xl">
            <div className="flex items-center justify-between p-4 border-b bg-gray-50">
              <div className="font-semibold">Delivery Challan - {previewDC.challanNumber || ''}</div>
              <div className="flex items-center gap-2">
                <button onClick={() => window.print()} className="inline-flex items-center px-3 py-1.5 bg-indigo-600 text-white rounded-lg text-sm hover:bg-indigo-700">
                  <FiPrinter className="mr-2" /> Print / Save PDF
                </button>
                <button onClick={closePreview} className="inline-flex items-center px-3 py-1.5 border rounded-lg text-sm">
                  <FiX className="mr-1" /> Close
                </button>
              </div>
            </div>
            <div className="p-4 max-h-[80vh] overflow-auto">
              <DeliveryChallanPreview challan={previewDC} autoPrint={false} />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
