import React from 'react';
import { NavLink } from 'react-router-dom';
import { FiHome, FiFileText, FiTruck, FiActivity, FiLogOut, FiMenu, FiX } from 'react-icons/fi';
import { useAuth } from '../../context/AuthContext';

export default function VendorSidebar() {
  const { logout, vendorProfile } = useAuth();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const linkClass = ({ isActive }) => `flex items-center px-4 py-3 rounded-lg transition-all duration-200 ${
    isActive ? 'bg-indigo-50 text-indigo-700' : 'text-gray-700 hover:bg-gray-100'
  }`;

  return (
    <>
      {/* Mobile toggle button */}
      <button
        className="md:hidden fixed top-3 left-3 z-40 inline-flex items-center justify-center w-10 h-10 rounded-lg bg-white shadow border border-gray-200"
        onClick={() => setMobileOpen(true)}
        aria-label="Open menu"
      >
        <FiMenu />
      </button>

      {/* Desktop sidebar */}
      <aside className="w-64 hidden md:block bg-white border-r border-gray-200 min-h-screen p-4">
        <div className="mb-6">
          <div className="text-xs text-gray-500">Signed in as</div>
          <div className="font-semibold text-gray-900">{vendorProfile?.name || 'Vendor'}</div>
        </div>
        <nav className="space-y-2">
          <NavLink to="/vendor" className={linkClass} end>
            <FiHome className="mr-3" /> Dashboard
          </NavLink>
          <NavLink to="/vendor/pos" className={linkClass}>
            <FiFileText className="mr-3" /> Purchase Orders
          </NavLink>
          <NavLink to="/vendor/delivery-challans" className={linkClass}>
            <FiTruck className="mr-3" /> Delivery Challans
          </NavLink>
          <NavLink to="/vendor/ongoing-work" className={linkClass}>
            <FiActivity className="mr-3" /> Ongoing Work
          </NavLink>
        </nav>
        <button onClick={logout} className="mt-6 inline-flex items-center px-4 py-2 text-sm text-gray-600 hover:text-gray-900">
          <FiLogOut className="mr-2" /> Logout
        </button>
      </aside>

      {/* Mobile overlay menu */}
      {mobileOpen && (
        <div className="md:hidden fixed inset-0 z-50 bg-black/40" onClick={() => setMobileOpen(false)}>
          <div className="absolute left-0 top-0 bottom-0 w-72 bg-white border-r border-gray-200 p-4" onClick={(e)=>e.stopPropagation()}>
            <div className="flex items-center justify-between mb-4">
              <div>
                <div className="text-xs text-gray-500">Signed in as</div>
                <div className="font-semibold text-gray-900">{vendorProfile?.name || 'Vendor'}</div>
              </div>
              <button className="p-2 rounded hover:bg-gray-100" onClick={() => setMobileOpen(false)} aria-label="Close menu">
                <FiX />
              </button>
            </div>
            <nav className="space-y-2">
              <NavLink to="/vendor" className={linkClass} end onClick={()=>setMobileOpen(false)}>
                <FiHome className="mr-3" /> Dashboard
              </NavLink>
              <NavLink to="/vendor/pos" className={linkClass} onClick={()=>setMobileOpen(false)}>
                <FiFileText className="mr-3" /> Purchase Orders
              </NavLink>
              <NavLink to="/vendor/delivery-challans" className={linkClass} onClick={()=>setMobileOpen(false)}>
                <FiTruck className="mr-3" /> Delivery Challans
              </NavLink>
              <NavLink to="/vendor/ongoing-work" className={linkClass} onClick={()=>setMobileOpen(false)}>
                <FiActivity className="mr-3" /> Ongoing Work
              </NavLink>
            </nav>
            <button onClick={logout} className="mt-6 inline-flex items-center px-4 py-2 text-sm text-gray-600 hover:text-gray-900">
              <FiLogOut className="mr-2" /> Logout
            </button>
          </div>
        </div>
      )}
    </>
  );
}
