import React, { useEffect, useMemo, useState } from 'react';
import { FiDownload } from 'react-icons/fi';
import { db } from '../../firebase';
import { doc, getDoc } from 'firebase/firestore';

// Shared operations list used across PO screens
const operations = [
  { value: 'HBL', label: 'HBL (Horizontal Boring & Lathe)' },
  { value: 'VTL', label: 'VTL (Vertical Turning Lathe)' },
  { value: 'Drilling', label: 'Drilling' },
  { value: 'VMC', label: 'VMC (Vertical Machining Center)' }
];

export default function POPrintPreview({ poData, vendor, vendorId, onClose, autoPrint = false }) {
  const [resolvedVendor, setResolvedVendor] = useState(vendor || null);

  useEffect(() => {
    let ignore = false;
    const fetchVendor = async () => {
      if (!vendor && vendorId) {
        try {
          const snap = await getDoc(doc(db, 'vendors', vendorId));
          if (!ignore && snap.exists()) setResolvedVendor({ id: snap.id, ...snap.data() });
        } catch (_) {}
      }
    };
    fetchVendor();
    return () => { ignore = true; };
  }, [vendor, vendorId]);

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-IN', {
      maximumFractionDigits: 2,
      minimumFractionDigits: 2,
    }).format(Number(amount || 0));
  };

  const numberToWords = (num) => {
    const ones = ['', 'One', 'Two', 'Three', 'Four', 'Five', 'Six', 'Seven', 'Eight', 'Nine'];
    const teens = ['Ten', 'Eleven', 'Twelve', 'Thirteen', 'Fourteen', 'Fifteen', 'Sixteen', 'Seventeen', 'Eighteen', 'Nineteen'];
    const tens = ['', 'Ten', 'Twenty', 'Thirty', 'Forty', 'Fifty', 'Sixty', 'Seventy', 'Eighty', 'Ninety'];
    if (num === 0) return 'Zero';
    let words = '';
    if (Math.floor(num / 10000000) > 0) { words += numberToWords(Math.floor(num / 10000000)) + ' Crore '; num %= 10000000; }
    if (Math.floor(num / 100000) > 0) { words += numberToWords(Math.floor(num / 100000)) + ' Lakh '; num %= 100000; }
    if (Math.floor(num / 1000) > 0) { words += numberToWords(Math.floor(num / 1000)) + ' Thousand '; num %= 1000; }
    if (Math.floor(num / 100) > 0) { words += numberToWords(Math.floor(num / 100)) + ' Hundred '; num %= 100; }
    if (num > 0) {
      if (words !== '') words += 'and ';
      if (num < 10) words += ones[num];
      else if (num < 20) words += teens[num - 10];
      else { words += tens[Math.floor(num / 10)]; if (num % 10 > 0) words += ' ' + ones[num % 10]; }
    }
    return words.trim();
  };

  const amountInWords = useMemo(() => {
    const n = Math.floor(parseFloat(poData?.finalAmount || 0));
    const paise = Math.round(((parseFloat(poData?.finalAmount || 0) % 1) || 0) * 100);
    return (
      numberToWords(n) + ' Rupees' + (paise ? ` and ${paise} Paise` : '')
    );
  }, [poData?.finalAmount]);

  const handlePrint = () => window.print();

  useEffect(() => {
    if (autoPrint) {
      // small timeout allows the DOM to render before printing
      const t = setTimeout(() => window.print(), 100);
      return () => clearTimeout(t);
    }
  }, [autoPrint]);

  const opsText = useMemo(() => {
    const ops = Array.isArray(poData?.operations) ? poData.operations : (poData?.operation ? [poData.operation] : []);
    return ops.map(v => operations.find(o => o.value === v)?.label || v).join(', ');
  }, [poData]);

  const formatDate = (val) => {
    try {
      if (!val) return null;
      let d = val;
      if (typeof val?.toDate === 'function') d = val.toDate();
      else if (typeof val === 'string') d = new Date(val);
      return d.toLocaleDateString('en-IN', { day: '2-digit', month: 'short', year: 'numeric' });
    } catch (_) {
      return null;
    }
  };

  return (
    <div className="print-overlay fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-5xl w-full max-h-[90vh] overflow-y-auto">
        <style>{`
          @media print {
            @page { size: A4 portrait; margin: 10mm 15mm 15mm 15mm; }
            body { -webkit-print-color-adjust: exact; print-color-adjust: exact; background: white !important; }
            * { background: transparent !important; box-shadow: none !important; text-shadow: none !important; }
            body * { visibility: hidden !important; }
            .print-overlay, .print-overlay * { visibility: visible !important; }
            .print-overlay { position: static !important; inset: auto !important; padding: 0 !important; background: transparent !important; overflow: visible !important; }
            html, body { overflow: visible !important; height: auto !important; width: 210mm !important; margin: 0 !important; padding: 0 !important; }
            #po-preview { width: 100% !important; margin: 0 !important; padding: 0 !important; }
            .no-print { display: none !important; }
            .print-a4 { box-shadow: none !important; margin: 0 !important; padding: 0 !important; }
            .page-break { page-break-after: always; }
          }
          .print-a4 { max-width: 210mm; margin: 18px auto; background: white; font-family: 'Helvetica','Arial',sans-serif; line-height: 1.35; color: #000; font-size: 12px; }
          .company-header { border-bottom: 2px solid #2c5282; padding-bottom: 10px; margin-bottom: 15px; }
          .po-title { background: #2c5282; color: white; padding: 8px 15px; font-weight: bold; font-size: 16px; text-align: center; border-radius: 4px; margin: 10px 0; }
          .section-title { font-size: 13px; font-weight: bold; padding-bottom: 4px; margin-bottom: 8px; border-bottom: 1px solid #ccc; text-transform: uppercase; color: #2c5282; }
          .table { width: 100%; border-collapse: collapse; margin: 10px 0; }
          .table th { background: #f0f4f8; border: 1px solid #ccc; padding: 6px 8px; font-size: 11px; font-weight: bold; text-align: left; }
          .table td { border: 1px solid #ccc; padding: 6px 8px; font-size: 11px; vertical-align: top; }
          .table-footer td { font-weight: bold; background: #f8f9fa; }
          .signature-area { margin-top: 40px; border-top: 1px dashed #ccc; padding-top: 15px; }
          .terms-box { border: 1px solid #ccc; padding: 10px; margin: 10px 0; background: #f9f9f9; font-size: 11px; }
          .amount-in-words { padding: 8px; background: #f0f4f8; border: 1px solid #ccc; margin: 10px 0; font-style: italic; }
          .footer { margin-top: 30px; font-size: 10px; text-align: center; color: #666; }
        `}</style>

        <div className="p-4 border-b border-gray-200 flex justify-between items-center no-print">
          <h2 className="text-xl font-semibold text-gray-900">Print Purchase Order</h2>
          <div className="flex space-x-2">
            <button onClick={handlePrint} className="inline-flex items-center px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700">
              <FiDownload className="mr-2" size={16} /> Print / Save PDF
            </button>
            <button onClick={onClose} className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50">Close</button>
          </div>
        </div>

        <div className="print-a4 p-10" id="po-preview">
          <div className="company-header">
            <div className="flex justify-between items-start">
              <div>
                <div className="text-xl font-bold">THORAT VALVES PVT. LTD.</div>
                <div className="text-sm">607, Shaniwar Peth, Satara - 415002</div>
                <div className="text-sm">Phone: +91-2162-270262 • Email: thoratvalves@gmail.com</div>
                <div className="text-sm">GSTIN: 27AAACT0000Z1Z5</div>
              </div>
              <div className="text-right">
                <div className="text-sm">PO Reference: {poData?.poNumber || `PO${new Date().getFullYear()}${String(new Date().getMonth()+1).padStart(2,'0')}${String(new Date().getDate()).padStart(2,'0')}`}</div>
                <div className="text-sm">Date: {formatDate(poData?.createdAt) || formatDate(new Date())}</div>
              </div>
            </div>
          </div>

          <div className="po-title">PURCHASE ORDER</div>

          <div className="grid grid-cols-2 gap-8 mb-6">
            <div>
              <table className="details-table">
                <tbody>
                  <tr><td className="font-bold pr-4">PO Number:</td><td>{poData?.poNumber || '-'}</td></tr>
                  <tr><td className="font-bold">PO Date:</td><td>{new Date().toLocaleDateString('en-IN', { day: '2-digit', month: 'short', year: 'numeric' })}</td></tr>
                  <tr><td className="font-bold">Operation(s):</td><td>{opsText || 'Not Specified'}</td></tr>
                </tbody>
              </table>
            </div>
            <div>
              <table className="details-table">
                <tbody>
                  <tr><td className="font-bold pr-4">Delivery By:</td><td>{formatDate(poData?.deliveryDate) || 'TBD'}</td></tr>
                  <tr><td className="font-bold">Payment Terms:</td><td>{poData?.paymentTerms || '-'} Days</td></tr>
                  <tr><td className="font-bold">State of Supply:</td><td>{poData?.stateOfSupply || '-'}</td></tr>
                </tbody>
              </table>
            </div>
          </div>

          <div className="mb-6">
            <div className="section-title">VENDOR DETAILS</div>
            {resolvedVendor ? (
              <div className="grid grid-cols-2 gap-8">
                <div>
                  <div className="font-bold">{resolvedVendor.name}</div>
                  <div className="whitespace-pre-line">{resolvedVendor.address || 'Address not specified'}</div>
                  <div>GSTIN: {resolvedVendor.gstin || 'Not provided'}</div>
                </div>
                <div>
                  <div><span className="font-bold">Contact:</span> {resolvedVendor.contactPerson || 'Not specified'}</div>
                  <div><span className="font-bold">Phone:</span> {resolvedVendor.phone || 'Not provided'}</div>
                  <div><span className="font-bold">Email:</span> {resolvedVendor.email || 'Not provided'}</div>
                </div>
              </div>
            ) : (
              <div>Vendor details not available</div>
            )}
          </div>

          <div className="mb-6">
            <div className="section-title">ITEMS / SERVICES</div>
            <table className="table">
              <thead>
                <tr>
                  <th style={{width: '5%'}}>Sr No.</th>
                  <th style={{width: '35%'}}>Description</th>
                  <th style={{width: '10%'}}>Operation</th>
                  <th style={{width: '8%'}}>Qty</th>
                  <th style={{width: '8%'}}>Unit</th>
                  <th style={{width: '12%'}}>Rate (₹)</th>
                  <th style={{width: '14%'}}>Amount (₹)</th>
                </tr>
              </thead>
              <tbody>
                {(poData?.items || []).map((item, index) => (
                  <tr key={index}>
                    <td className="text-center">{index + 1}</td>
                    <td>
                      <div className="font-medium">{item.description}</div>
                      {item.specifications && (
                        <div className="text-xs mt-1 text-gray-600">{item.specifications}</div>
                      )}
                    </td>
                    <td>{item.operation ? (operations.find(o => o.value === item.operation)?.label || item.operation) : '-'}</td>
                    <td className="text-center">{item.quantity}</td>
                    <td className="text-center">{item.unit}</td>
                    <td className="text-right">{formatCurrency(parseFloat(item.unitPrice || 0))}</td>
                    <td className="text-right">{formatCurrency(parseFloat(item.amount || 0))}</td>
                  </tr>
                ))}
              </tbody>
              <tfoot className="table-footer">
                <tr>
                  <td colSpan="5" className="text-right font-bold">Subtotal:</td>
                  <td colSpan="2" className="text-right">{formatCurrency(parseFloat(poData?.totalAmount || 0))}</td>
                </tr>
                {parseFloat(poData?.discountAmount) > 0 && (
                  <tr>
                    <td colSpan="5" className="text-right font-bold">Discount:</td>
                    <td colSpan="2" className="text-right">-{formatCurrency(parseFloat(poData?.discountAmount || 0))}</td>
                  </tr>
                )}
                {parseFloat(poData?.taxAmount) > 0 && (
                  <tr>
                    <td colSpan="5" className="text-right font-bold">GST ({poData?.taxRate}%):</td>
                    <td colSpan="2" className="text-right">{formatCurrency(parseFloat(poData?.taxAmount || 0))}</td>
                  </tr>
                )}
                {poData?.roundOffEnabled && (
                  <tr>
                    <td colSpan="5" className="text-right font-bold">Round Off:</td>
                    <td colSpan="2" className="text-right">{formatCurrency(parseFloat(poData?.roundOffValue || 0))}</td>
                  </tr>
                )}
                <tr>
                  <td colSpan="5" className="text-right font-bold">TOTAL (₹):</td>
                  <td colSpan="2" className="text-right font-bold">{formatCurrency(parseFloat(poData?.finalAmount || 0))}</td>
                </tr>
              </tfoot>
            </table>
          </div>

          <div className="amount-in-words">
            <span className="font-bold">Amount in Words: </span>
            {amountInWords} Only
          </div>

          {poData?.terms && (
            <div className="mb-6">
              <div className="section-title">TERMS AND CONDITIONS</div>
              <div className="terms-box">
                <div className="whitespace-pre-line">{poData.terms}</div>
              </div>
            </div>
          )}

          {poData?.description && (
            <div className="mb-6">
              <div className="section-title">SPECIAL INSTRUCTIONS / WORK DESCRIPTION</div>
              <div className="p-3 border border-gray-300">
                {poData.description}
              </div>
            </div>
          )}

          <div className="signature-area">
            <div className="grid grid-cols-2 gap-8">
              <div>
                <div className="font-bold mb-2">For THORAT VALVES PVT. LTD.</div>
                <div className="h-16 border-b border-gray-400"></div>
                <div className="text-sm mt-1">Authorized Signatory</div>
                <div className="text-sm">Prepared By: (D.R. Naik)</div>
              </div>
              <div>
                <div className="font-bold mb-2">VENDOR ACCEPTANCE</div>
                <div className="h-16 border-b border-gray-400"></div>
                <div className="text-sm mt-1">Authorized Signatory</div>
                <div className="text-sm">Date: __________________________</div>
                <div className="text-sm">Company Seal</div>
              </div>
            </div>
          </div>

          <div className="footer">
            <div>This is a computer generated document and does not require a physical signature</div>
            <div>Page 1 of 1</div>
          </div>
        </div>
      </div>
    </div>
  );
}
